.button span {
  height: 100% !important;
}

.labelPdf {
  display: flex;
  text-align: center;
}

.pdfView {
  display: flex;
  height: 100vh;
}

.pdfContent {
  width: auto;
}

.divPrincipal {
  display: flex; 
  align-items: center; 
  justify-content: space-between; 
  padding: 10;
  flex-direction: row;
}

.memor2 {
  flex-direction: row;
}

.inputmemor1 {
  width: 50%;
}

.inputmemor2 {
  width: 50%;
  margin-top: 0;
  margin-left: 10px;
}

.tamanhoMenor {
  width: 60vw;
}

.tamanhoMenor1 {
  width: 35vw;
}

.tamanhoMenor3 {
  width: 35vw;
}

@media only screen and (max-width: 1000px) {
  .memor2 {
    flex-direction: column;
  }
  .inputmemor1 {
    width: auto
  }

  .inputmemor2 {
    width: auto;
    margin-top: 10px;
    margin-left: 0;
  }
}

@media only screen and (max-width: 500px) {
  .pdfView {
    display: none;
  }
  .labelPdf {
    display: none;
  }
}

@media only screen and (max-width: 1740px) {
  .pdfContent {
    width: 95%;
  }

  .divPrincipal {
    flex-direction: column;
  }
  .tamanhoMenor {
    width: 93%;
    margin-bottom: 20px;
  }

  .tamanhoMenor1 {
    margin-top: 10px;
    width: 93%;
  }
  
  .tamanhoMenor3 {
    margin-top: 10px;
    width: 100%;
  }
}